@mixin list-normilize {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin main-text {
  font-size: 14px;
  line-height: 24px;
  color: $text-dark;
}

@mixin button {
  display: inline-block;
  text-decoration: none;
  font-size: 18px;
  font-weight: 700;
  line-height: 18px;
  padding: 15px 40px;
  border-radius: 50px;
}
